html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  width: 120px;
  height: 31px;
  margin: 16px -10px 16px -30px;
  border-radius: 45px;
  float: left;
  background: conic-gradient(
    from 243.76deg at 92.73% 45.32%,
    #c4c4c4 -24.37deg,
    #4285f4 68.12deg,
    #c4c4c4 335.63deg,
    #4285f4 428.12deg
  );
}

.header {
  background-color: #fdfdfe !important;
  position: fixed !important;
  width: 100%;
  z-index: 10 !important;
}

.c-head {
  background-color: #fdfdfe;
  float: right;
  margin-right: -30px;
  border: none;
}

.c-headA {
  float: right;
  border: none;
}

.sider-c {
  margin-top: 64px;
  position: fixed;
  overflow-y: scroll;
  z-index: 1;
  left: 0;
  top: 0;
  bottom: 0;
  height: 92vh;
}

.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
  color: #000000;
}
.trigger:hover {
  color: blue;
}

.name-user {
  font-weight: bold;
}

@media only screen and (min-width: 600px) {
  .sider-c {
    display: hidden;
    overflow: auto !important;
    z-index: 1;
    left: 0;
  }
}
@media only screen and (max-width: 752px) {
  .logo {
    display: none;
  }
}

thead tr th {
  background-color: #1890ff !important;
}
thead tr td {
  background-color: #1890ff !important;
}
.bg-red {
  background-color: #fc8282 !important;
  color: #000000 !important;
}
.bg-green {
  background-color: #afdc82 !important;
  color: #000000 !important;
}

.content-detail {
  height: 51.2vh;
  padding: 10;
}
.virtual-list-data {
  height: 51.2vh;
}
.content-cover {
  overflow-y: scroll;
  /* background: #d3d3d3; */
}

.content-cover::-webkit-scrollbar {
  width: 10px;
}

.content-cover::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(127, 127, 127, 0.5);
}

.ant-layout-content::-webkit-scrollbar {
  width: 10px;
}
.ant-layout-content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(127, 127, 127, 0.5);
}

.ant-layout-sider::-webkit-scrollbar {
  width: 10px;
}
.ant-layout-sider::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(127, 127, 127, 0.5);
}
.ant-card::-webkit-scrollbar {
  width: 10px;
}
.ant-card::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(127, 127, 127, 0.5);
}
.ant-list-item {
  border-block-end: 1px solid rgba(5, 5, 5, 0.3) !important;
}
.ant-list-item:hover {
  background-color: #b0c4de71;
  border-radius: 5%;
}
.ant-list-item {
  overflow: hidden;
}
.ant-list-item-meta-content {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis !important;
}
